html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

html,
body {
  height: 100%; }

body {
  font-family: "Alice", serif; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-size: inherit;
  font-weight: 400; }

form {
  margin: 0; }

fieldset {
  padding: 0;
  border: 0;
  margin: 0; }

a {
  text-decoration: none;
  color: inherit; }

address {
  font-style: inherit; }

p {
  margin: 0; }

ul,
ol,
li {
  margin: 0;
  padding: 0;
  list-style: none; }

button {
  display: block;
  background: none;
  border: none;
  padding: 0;
  cursor: pointer; }

input {
  border: inherit;
  padding: 0;
  margin: 0; }

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%; }

.main {
  flex: 1 0 auto; }

.footer {
  flex: 0 0 auto; }

.container {
  max-width: 1200px;
  width: 100%;
  padding: 0 15px 0;
  margin: 0 auto; }

.visually-hidden {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px; }

.reset-button {
  margin: 0;
  border: 0;
  border: 1px solid black;
  padding: 0;
  font-family: inherit;
  text-align: left;
  color: inherit;
  background-color: transparent;
  outline: none;
  cursor: pointer; }
  .reset-button:disabled {
    cursor: default; }

.text {
  font-family: "Montserrat", sans-serif;
  font-size: 18px;
  opacity: 0.8; }

.sub-title {
  font-size: 64px;
  color: #424242; }

.link {
  margin-left: 30px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 10px 42px;
  border: 1px solid #424242;
  color: #424242;
  background-color: #fff;
  transition: 0.3s all; }
  .link span {
    font-size: 18px;
    margin-left: 10px; }
  .link svg {
    width: 24px;
    height: 24px; }
    .link svg path {
      stroke: #424242;
      transition: 0.3s all; }
  .link:hover, .link:active {
    color: #fff; }
    .link:hover svg path, .link:active svg path {
      stroke: #fff; }
  .link:hover {
    background-color: #424242; }
  .link:active {
    background-color: #828387;
    border: 1px solid #828387; }

.button {
  padding: 10px 40px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background-color: #424242;
  border: 1px solid #fff;
  max-width: 180px;
  width: 100%;
  height: 44px;
  transition: 0.3s all; }
  .button svg path {
    stroke: #fff;
    transition: 0.3s all; }
  .button:disabled {
    border: 1px solid #828387; }
    .button:disabled svg path {
      stroke: #828387; }
  .button:hover {
    background-color: #fff;
    border: 1px solid #fff; }
    .button:hover svg path {
      stroke: #424242; }
  .button:active {
    background-color: #e0e0e0;
    border: 1px solid #e0e0e0; }
    .button:active svg path {
      stroke: #424242; }

.main-button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  max-width: 376px;
  width: 100%;
  height: 49px;
  font-size: 24px;
  padding: 10px;
  background-color: #424242;
  color: #fff;
  border: 1px solid #fff;
  transition: 0.3s all; }
  .main-button:hover, .main-button:active {
    color: #424242; }
  .main-button:hover {
    background-color: #fff; }
  .main-button:active {
    background-color: #e0e0e0; }

.overlay {
  display: none;
  position: absolute;
  z-index: 5;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); }
  .overlay.active {
    display: block; }

@font-face {
  font-family: "Montserrat";
  src: url(../fonts/Montserrat-Regular.ttf) format("ttf"), url(../fonts/Montserrat-Regular.woff) format("woff"), url(../fonts/Montserrat-Regular.woff2) format("woff2");
  font-display: swap;
  font-weight: 400;
  font-style: normal; }

:root {
  --header-color: #fff; }

.header {
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 1;
  padding: 30px 0; }
  .header__container {
    color: var(--header-color); }
  .header__avatar {
    display: none;
    width: 32px;
    height: 32px;
    position: relative;
    border-radius: 58px;
    background-color: #424242;
    overflow: hidden; }
  .header__avatar-img {
    width: 100%;
    height: 100%;
    position: absolute;
    display: inline-block;
    object-fit: cover;
    object-position: center; }
  .header__nav {
    display: flex; }
  .header__burger {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 7px 0; }
    .header__burger span {
      width: 30px;
      height: 2px;
      display: inline-block;
      background-color: var(--header-color); }
  .header__list {
    margin: 0 auto;
    display: flex;
    gap: 0 87px; }
  .header__list-link {
    text-transform: uppercase;
    font-size: 21px;
    color: var(--header-color);
    font-weight: 400; }
  .header__lang {
    display: flex;
    gap: 0 10px; }
  .header__lang-btn {
    font-size: 21px;
    text-transform: uppercase;
    color: var(--header-color);
    opacity: 0.5; }
    .header__lang-btn.active {
      opacity: 1; }

.promo {
  max-width: 100vw;
  width: 100%;
  min-height: 100vh;
  height: auto;
  background-image: url(../images/promo__bg.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  display: flex;
  align-items: center;
  position: relative; }
  .promo__container {
    color: #fff;
    display: flex;
    flex-direction: column;
    position: relative; }
  .promo__date {
    display: flex;
    align-items: center;
    margin-bottom: 40px; }
    .promo__date span:first-child,
    .promo__date span:last-child {
      font-size: 32px;
      font-style: normal; }
    .promo__date span:nth-child(2) {
      display: inline-block;
      width: 99px;
      height: 1.5px;
      background-color: #fff;
      margin: 0 9px; }
  .promo__cite {
    font-size: 96px;
    font-style: normal;
    font-weight: 400;
    line-height: 86.8%;
    margin-bottom: 40px; }
  .promo__quoute {
    font-size: 24px;
    max-width: 361px;
    width: 100%;
    position: relative;
    padding-top: 76px; }
    .promo__quoute::after, .promo__quoute::before {
      content: ""; }
    .promo__quoute::before {
      width: 45px;
      height: 36px;
      top: 0;
      left: 0;
      position: absolute;
      background-image: url(../images/promo__quotes.svg);
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover; }
  .promo__mouse {
    position: absolute;
    width: 38px;
    height: 63px;
    bottom: -200px;
    left: 0; }

.menu {
  width: 491px;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 2;
  background: #424242;
  padding-right: 75px;
  padding-left: 137px;
  padding-top: 41px;
  min-height: 100vh;
  height: auto;
  box-shadow: 19px 3px 42px 0px rgba(0, 0, 0, 0.1), 75px 12px 76px 0px rgba(0, 0, 0, 0.09), 170px 28px 103px 0px rgba(0, 0, 0, 0.05), 301px 49px 122px 0px rgba(0, 0, 0, 0.01), 471px 77px 134px 0px rgba(0, 0, 0, 0);
  color: #fff;
  display: flex;
  flex-direction: column;
  z-index: 100;
  visibility: hidden;
  transform: translateX(-100%);
  transition: all cubic-bezier(0.81, 0.16, 0.41, 0.98) 0.5s; }
  .menu.active {
    visibility: visible;
    transform: translateX(0); }
  .menu__close-btn {
    margin-left: auto; }
  .menu__list {
    display: flex;
    flex-direction: column;
    gap: 40px 0; }
  .menu__list-item {
    font-size: 21px; }
  .menu__list-btn {
    width: 100%;
    text-align: left;
    color: #fff; }
    .menu__list-btn.active span::after {
      transform: rotate(0deg); }
    .menu__list-btn span {
      position: relative;
      pointer-events: none; }
      .menu__list-btn span::after {
        content: "";
        position: absolute;
        background-image: url(../images/menu__arrow.svg);
        width: 24px;
        height: 24px;
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        transition: 0.3s transform;
        transform: rotate(180deg); }
  .menu__dropdown {
    display: flex;
    flex-direction: column;
    gap: 23px 0;
    padding-left: 20px;
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.4s ease-out, padding-top 0.4s ease-out; }
  .menu__dropdown-link {
    display: inline-block;
    width: 100%;
    font-size: 18px;
    color: rgba(255, 255, 255, 0.7); }
  .menu__lang {
    margin-top: auto;
    display: flex;
    gap: 0 10px;
    margin-bottom: 19px; }
  .menu__lang-btn {
    color: #fff;
    font-size: 21px;
    opacity: 0.5;
    transform: 0.3s opacity; }
    .menu__lang-btn.active {
      opacity: 1; }
  .menu__socials {
    display: flex;
    gap: 0 10px;
    align-self: flex-start;
    margin-bottom: 104px; }

@media screen and (max-width: 1024px) {
  .header__list {
    gap: 0 25px; } }

@media screen and (max-width: 768px) {
  .header {
    padding-top: 15px 0; }
    .header__list {
      display: none; }
    .header__avatar {
      display: inline-block;
      margin-right: auto; }
    .header__burger {
      order: 3; }
    .header__lang {
      gap: 0 10px; }
    .header__lang-btn {
      font-size: 14px;
      margin-right: 19px; }
  .menu {
    max-width: 100vw;
    width: 100%;
    padding: 33px 20px; }
    .menu__lang {
      margin-bottom: 11px; }
    .menu__lang-btn {
      font-size: 18px; }
    .menu__socials {
      margin-bottom: 0; }
    .menu__list {
      gap: 33px 0; }
    .menu__list-btn span {
      padding-right: 23px; }
      .menu__list-btn span::after {
        width: 18px;
        height: 18px;
        right: 0; }
    .menu__list-item {
      font-size: 18px; }
    .menu__dropdown {
      gap: 17px 0; }
    .menu__dropdown-link {
      font-size: 16px; } }

@media screen and (max-width: 499px) {
  .promo {
    background-image: url(../images/promo-__bg-sm.jpg); }
    .promo__date {
      margin-bottom: 8px; }
      .promo__date span:last-child,
      .promo__date span:first-child {
        font-size: 21px; }
      .promo__date span:nth-child(2) {
        width: 23px;
        margin: 0 6px; }
    .promo__cite {
      font-size: 32px;
      line-height: 100%;
      margin-bottom: 12px; }
    .promo__quoute {
      font-size: 16px;
      max-width: 174px;
      width: 100%;
      padding-top: 24px; }
      .promo__quoute::before {
        width: 19px;
        height: 16px;
        background-image: url(../images/promo__quotes-sm.svg); }
    .promo__mouse {
      display: none; } }
